
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Card from 'vux/src/components/card/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

export default {
name: "Recommand",
data() {
return {
loading: false,
radioList: [],
slider: [],
songList: [],
msg: "Recommand"
};
},
created() {
this.loading = true;
this.$store
.dispatch("getRecommands")
.then(
response => {
this.$vux.toast.show({
type: "text",
position: "top",
text: "获取数据成功"
});
let _data = response.data.data;
this.radioList = _data.radioList;
this.slider = _data.slider;
this.songList = _data.songList;
},
responce => {
this.$vux.toast.show({
type: "text",
position: "top",
text: "获取数据失败"
});
}
)
.then(responce => {
this.loading = false;
});
},
methods: {
onHotsClick(item) {}
},
watch: {
loading(val) {
if (val) {
this.$vux.loading.show({
text: "正在更新数据"
});
} else {
this.$vux.loading.hide();
}
}
},
components: {
Flexbox,
FlexboxItem,
Card,
XImg,
Swiper,
SwiperItem,
Group,
CellBox
}
};



