
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

export default {
name: "RankPage",
data() {
return {
loading: false,
topList: []
};
},
created() {
this.loading = true;
this.$store
.dispatch("getRankList")
.then(
response => {
this.$vux.toast.show({
type: "text",
position: "top",
text: "获取数据成功"
});
let _data = response.data.data;
this.topList = _data.topList;
},
responce => {
this.$vux.toast.show({
type: "text",
position: "top",
text: "获取数据失败"
});
}
)
.then(responce => {
this.loading = false;
});
},
watch: {
loading(val) {
if (val) {
this.$vux.loading.show({
text: "正在更新数据"
});
} else {
this.$vux.loading.hide();
}
}
},
components: {
Flexbox,
FlexboxItem,
XImg,
Group,
Cell,
CellFormPreview,
CellBox
},
filters: {
listenCount: num=> {
return Math.round(num / 1000) / 10 + '万'
}
}
};



